import axios from 'axios'

axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'

function getResponse (data) {
  return (data && data.data && data.data.response) || []
}

const api = {
  // home banners
  async getBanners () {
    const { data = {} } = await axios.get('/index/index/getBanner')
    if (data.code === 1) {
      return data.data
    }
    return []
  },

  // 获取首页新闻列表
  async getHomeNewsList (page = 1, title = '') {
    const body = { page }
    if (title) {
      body.title = title
    }
    const { data = {} } = await axios.post('/index/index/index', body)
    return getResponse(data)
  },

  // 根据经纬度获取地理位置：城市
  async getLocationCity (lat, lng) {
    const { data = {} } = await axios.post('/index/index/getCity', {
      lat,
      lng
    })
    if (data.code === 1) {
      return data.data.city
    }
    return ''
  },

  // 获取微信签名
  async getWechatSign (url) {
    const { data = {} } = await axios.post('/index/jssdk/getSignPackage', { url })
    if (data.code === 1) {
      return data.data
    }
    return {}
  },

  // news list
  async getNewsList (id, page = 1, title = '') {
    const body = {
      channel_id: id,
      page
    }

    if (title) {
      body.title = title
    }

    const { data = {} } = await axios.post('/index/index/newsList', body)
    return getResponse(data)
  },

  // news detail
  async getNewsDetail (id) {
    const { data = {} } = await axios.post('/index/index/newsdetails', { id })
    return data && data.data
  },

  // 获取栏目列表
  async getChannel (parentId = 0) {
    const { data = {} } = await axios.post('/index/index/getChannel', {
      parent_id: parentId
    })
    return data.data
  },

  // search
  async search ({ url, title = '', options = {} }) {
    const body = Object.assign({ title }, options)
    const { data = {} } = await axios.post(url, body)
    return getResponse(data)
  },

  // qs list
  async getQsList (page = 1, title) {
    const body = { page }
    if (title) {
      body.title = title
    }
    const { data = {} } = await axios.post('/index/qs/index', body)
    return getResponse(data)
  },

  // activity select conditions
  async getSelectConditions () {
    const { data = {} } = await axios.get('/index/activity/searchCondition')
    if (data.code === 1) {
      return data.data
    }
    return {}
  },

  // activity list
  async getActivityList ({ page = 1, typeId, status }) {
    const body = { page }
    if (typeId !== null && typeId !== undefined) {
      body['type_id'] = typeId
    }
    if (status !== null && status !== undefined) {
      body.status = status
    }
    const { data = {} } = await axios.post('/index/activity/index', body)
    return getResponse(data)
  },

  // activity detail
  async getActivityDetail (id) {
    const { data = {} } = await axios.post('/index/activity/details', { id })
    return data.data && data.data.response
  },

  // activity 预约
  async appoitActivity ({ id, name, phone }) {
    const body = { id, name, phone }
    const { data = {} } = await axios.post('/index/activity/reservationActivity', body)
    return data
  },

  // matrix cities
  async getMatrixCitys () {
    const { data = {} } = await axios.get('/index/Matrix/index')
    return getResponse(data)
  },

  // matrix offcial accounts
  async getOffcialAccountList (id, status = 1) {
    const body = { id, status }
    const { data = {} } = await axios.post('/index/Matrix/wechatoffcialaccountsbyarea', body)
    return getResponse(data)
  },

  // live list
  async getLiveList (page = 1, title = '') {
    const body = { page }
    if (title) {
      body.title = title
    }
    const { data = {} } = await axios.post('/index/live/index', body)
    return getResponse(data)
  },

  // live detail
  async getLiveDetail (id) {
    const { data = {} } = await axios.post('/index/live/details', { id })
    return data.data && data.data.response
  }
}

export default api



