
import api from '@/utils/api'
import PostDetail from '@/components/PostDetail'
import Loading from 'vux/src/components/loading/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'


export default {
name: 'Post',
components: {
PostDetail,
Loading
},
data () {
return {
loading: true,
aid: '',
content: null
}
},
methods: {
getAid () {
this.aid = querystring.parse().aid
},
async fetchData () {
this.loading = true

try {
const data = await api.getNewsDetail(this.aid)
if (data) {
this.content = Object.assign({}, data)
document.title = this.content.title
}
} catch (err) {
console.error(err)
}
this.loading = false
}
},
created () {
this.getAid()
this.fetchData()
}
}



